import { ImgPers } from "../../common/Img";

import {
  ContentContainer,
  BlockContainer,
  Title,
  Wave,
  RoadmapContainer,
  RoadmapImage,
  RoadmapBlock,
  RoadmapBlock1,
  RoadmapBlock2,
  RoadmapBlock3,
  RoadmapBlock4,
  RoadmapBlock5,
} from "./styles";

const Roadmap = () => {

  return (
    <BlockContainer>

      <ContentContainer>

        <RoadmapContainer>

          <RoadmapBlock>
            <Title>Roadmap</Title>
          </RoadmapBlock>

          <RoadmapBlock1>
            <RoadmapImage>
              <ImgPers src="Roadmap1.jpg" width="100%" height="100%" />
            </RoadmapImage>
          </RoadmapBlock1>

          <RoadmapBlock2>
            <RoadmapImage>
              <ImgPers src="Roadmap2.jpg" width="100%" height="100%" />
            </RoadmapImage>
          </RoadmapBlock2>

          <RoadmapBlock3>
            <RoadmapImage>
              <ImgPers src="Roadmap3.jpg" width="100%" height="100%" />
            </RoadmapImage>
          </RoadmapBlock3>

          <RoadmapBlock4>
            <RoadmapImage>
              <ImgPers src="Roadmap4.jpg" width="100%" height="100%" />
            </RoadmapImage>
          </RoadmapBlock4>

          <RoadmapBlock5>
            <RoadmapImage>
              <ImgPers src="Roadmap5.jpg" width="100%" height="100%" />
            </RoadmapImage>
          </RoadmapBlock5>

        </RoadmapContainer>

      </ContentContainer>

      <Wave>
        <svg viewBox="0 0 500 80" preserveAspectRatio="none" style={{height: "100%", width: "100%", bottom: "0", left: "0", display: "block" }} >
          <path d="M0.00,49.98 C150.00,150.00 349.20,-50.00 500.00,49.98 L500.00,150.00 L0.00,190.00 Z" style={{stroke: "none", fill: "#FCFCA4", bottom: "0"}} />
        </svg>
      </Wave>

    </BlockContainer>
  );
};

export default (Roadmap);
